const { createElement, Fragment } = window.wp.element
const { registerFormatType, toggleFormat } = window.wp.richText
const { RichTextToolbarButton, RichTextShortcut } = window.wp.editor;

[
  {
    name: 'sup',
    title: 'Superscript',
    character: '7',
    icon:"arrow-up"
  },
  {
    name: 'sub',
    title: 'Subscript',
    character: '8',
    icon:"arrow-down"
  },
  {
    name:"small",
    title:"Small",
    character:"/",
    icon:"minus"
  },
  {
    name:"mark",
    title:"Marked",
    character:"=",
    icon:"tag"
  },
  {
    name:"ins",
    title:"Inserted",
    character:"+",
    icon:"yes"
  },
  {
    name:"del",
    title:"Deleted",
    character:"-",
    icon:"dismiss"
  },
  {
    name:"cite",
    title:"Cite",
    character:"2",
    icon:"sticky"
  }

].forEach(({ name, title, character, icon }) => {
  const type = `advanced/${name}`

  registerFormatType(type, {
    title,
    tagName: name,
    className: null,
    edit ({ isActive, value, onChange }) {
      const onToggle = () => onChange(toggleFormat(value, { type }))

      return (
        createElement(Fragment, null,
          createElement(RichTextShortcut, {
            type: 'primary',
            character,
            onUse: onToggle
          }),
          createElement(RichTextToolbarButton, {
            title,
            onClick: onToggle,
            isActive,
            shortcutType: 'primary',
            shortcutCharacter: character,
            icon: icon,
            className: `toolbar-button-with-text toolbar-button__advanced-${name}`
          })
        )
      )
    }
  })
})
